.Comment {
  margin: 2.5rem 0;
  font-size: 1.05rem;
}

.Comment-header {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
}

.Comment-avatar {
  width: 1.25rem;
  height: 1.25rem;
  border-radius: 50%;
  margin-right: 0.5rem;
}

.Comment-author {
  text-decoration: none;
  color: inherit;
  font-weight: bold;
  margin-right: 0.5rem;
  -ms-flex: 1 1;
      flex: 1 1;
}

.Comment-info {
  opacity: 0.65;
}

.Comment-body {
  margin-top: 0.5rem;
}



